<template>
	<view>
		<u-popup v-model="isShow" mode="bottom" @close="closePop">
			<view style="margin-bottom: 26rpx;">
				<view class="card-item-show">
					<view>
						起重机
					</view>
					<view>
						{{taskData.crane_name}}
					</view>
				</view>
				<view class="card-item-show">
					<view>
						所属机构
					</view>
					<view>
						{{taskData.mechanism_name}}
					</view>
				</view>
				<view class="card-item-show">
					<view>
						设备类型
					</view>
					<view>
						{{taskData.device_type}}
					</view>
				</view>
				<view class="card-item-show">
					<view>
						设备型号
					</view>
					<view>
						{{taskData.device_model_mumber}}
					</view>
				</view>
				<view class="card-item-show">
					<view>
						保养对象
					</view>
					<view>
						{{taskData.maintain_target}}
					</view>
				</view>
				<view class="card-item-show">
					<view>
						保养级别
					</view>
					<view>
						{{taskData.maintain_level}}
					</view>
				</view>
				<view class="card-item-show">
					<view>
						保养内容
					</view>
					<view>
						{{taskData.maintain_detail}}
					</view>
				</view>
				<view class="card-item-show">
					<view>
						保养标准
					</view>
					<view>
						{{taskData.maintain_standard}}
					</view>
				</view>
				<view class="card-item-show">
					<view>
						方法及工器具
					</view>
					<view>
						{{taskData.method_appliance}}
					</view>
				</view>
				<view class="card-item-show">
					<view>
						存在风险
					</view>
					<view>
						{{taskData.maintain_risk}}
					</view>
				</view>
				<view class="card-item-show">
					<view>
						防范措施
					</view>
					<view>
						{{taskData.precautionary_measure}}
					</view>
				</view>
				<view class="card-item-show">
					<view>
						保养类型
					</view>
					<view>
						{{taskData.maintain_type_name}}
					</view>
				</view>
				<view class="card-item-show">
					<view>
						保养周期
					</view>
					<view>
						{{taskData.maintain_cycle}}
					</view>
				</view>
				<view class="card-item-show">
					<view>
						保养开始时间
					</view>
					<view>
						{{taskData.maintain_start_time}}
					</view>
				</view>
				<view class="card-item-show">
					<view>
						保养到期时间
					</view>
					<view>
						{{taskData.maintain_end_time}}
					</view>
				</view>
				<view class="card-item-show">
					<view>
						保养人
					</view>
					<view>
						{{taskData.maintain_person}}
					</view>
				</view>
			</view>
		</u-popup>
	</view>
</template>

<script>
	export default {
		name: 'taskDetail',
		props: {
			visible: {
			  type: Boolean,
			  default: false
			},
			taskData: {
				type: Object,
				default: function() {
					return {}
				}
			}
		},
		data() {
			return {
				isShow: false
			}
		},
		watch: {
		  visible(val) {
		    this.isShow = val
		  }
		},
		created() {
		},
		methods: {
			closePop() {
				this.isShow = false
				this.$emit('update:visible', false)
				this.$emit('onClose')
			}
		}
	}
</script>

<style lang="scss">
	@charset "UTF-8";
	.card-item-show {
		display: flex;
		padding: 16rpx;
	
		:nth-child(1) {
			width: 25%;
			color: #686868
		}
	
		:nth-child(2) {
			width: 75%;
			text-align: right;
		}
	}
</style>